<!-- 我的关注 -->
<template>
  <div class="my-concerns">
    <div class="top-fixed">
      <div class="back" @click="back"></div>
      <div class="search" @click="searchClick"></div>
      <span class="edit-btn" @click="editHandle">编辑</span>
    </div>
    <div class="content">
      <div
        class="clone"
        v-show="cloneData.img"
        :class="[isTreans ? 'opacity0' : '']"
        :style="{
					left: cloneData.left + 'px',
					top: cloneData.top + 'px',
					transform: isTreans ? `translate3d(${cloneData.transX}px, ${cloneData.transY}px, 0)` : ''
				}"
      >
        <img :src="cloneData.img" alt />
        <h5>{{cloneData.title}}</h5>
      </div>
      <section>
        <h4>我的关注</h4>
        <ul style="border-bottom: 1px solid #EAEAEA;" class="myapp-list">
          <vuedraggable v-model="myAppList" :delay="100" v-bind="dragOptions">
            <transition-group type="transition">
              <li
                v-for="(item, index) in myAppList"
                :key="index"
                @click="toViewDetail(item.url)"
                :class="[
									isCanEdit ? 'showBorder' : '',
									delIndex === index ? 'trans' : '',
									'myapp-item'
								]"
              >
                <span class="add-btn" v-show="isCanEdit" @click.stop="delApp(index)"></span>
                <img :src="item.icon" alt />
                <h5>{{item.modulename}}</h5>
              </li>
            </transition-group>
          </vuedraggable>
        </ul>
      </section>
      <section>
        <h4>更多应用</h4>
        <ul class="more-list">
          <vuedraggable
            v-model="moreAppList"
            :sort="false"
            v-bind="dragOptions"
            :handle="'.del-btn'"
          >
            <transition-group type="transition">
              <li
                v-for="(item, index) in moreAppList"
                :key="index"
                @click="toViewDetail(item.url)"
                :class="[
									isCanEdit ? 'showBorder' : '',
									index > 3 ? 'margin' : '',
									addIndex === index ? 'trans' : ''
								]"
              >
                <span class="del-btn" v-show="isCanEdit" @click.stop="addApp(index, $event)"></span>
                <img :src="item.icon" alt />
                <h5>{{item.modulename}}</h5>
              </li>
            </transition-group>
          </vuedraggable>
        </ul>
      </section>
    </div>
    <div class="handle-bar" v-show="isCanEdit">
      <div class="cancel btn" @click="isCanEdit = false">取消</div>
      <div class="done btn" @click="complete">完成</div>
    </div>
  </div>
</template>
<script>
import vuedraggable from "vuedraggable";
import { Toast } from 'vant';
import SearchBar from "../../components/SearchBar";
const rootFontSize =
  document.documentElement.getBoundingClientRect().width / 3.75;
export default {
  name: "MyConcerns",
  components: {
    vuedraggable,
    SearchBar
  },
  data() {
    return {
      inputVal: "",
      delIndex: -1,
      addIndex: -1,
      isTreans: false,
      isCanEdit: false,
      myAppList: [],
      moreAppList: [],
      cloneData: {}
    };
  },
	mounted() {
    this.getList();
	},
	computed: {
    dragOptions() {
      return {
        animation: 300,
        group: "description",
        ghostClass: "ghost"
      };
    }
  },
	methods: {
    back() {
			this.$router.go(-1);
    },
    editHandle() {
      if (this.moreAppList.length === 0 && this.myAppList.length === 0) return;
      this.isCanEdit = true;
    },
    searchClick() {
      const idList = this.myAppList.map(item => item.moduleid);
      this.$router.push({name: 'MyConcernsSearch', params: {
        selectId: idList,
        allList: this.originList
      }});
    },
		getList() {
			this.$axios.post('/hopeModule/queryModuleSub', {
          // "aamid": this.$store.state.userInfoObj.aamid,
          // "deptid": this.$store.state.userInfoObj.deptid,
					// "odeptid": this.$store.state.userInfoObj.odeptid
					aamid: "010203",
          deptid: "0010101032",
          odeptid: "0010100003"
				}).then(res => {
					if (res.status === 200 && res.data.status === '000000') {
            const resData = res.data.data || [];
						const myAppList = [], moreAppList = [];
						resData.forEach(item => {
							if (item.wstatus === '01') {
								myAppList.push(item);
							} else {
								moreAppList.push(item);
							}
            });
            this.moreAppList = moreAppList;
            this.myAppList = myAppList.sort((a, b) => a.sequence - b.sequence);
            this.originList = resData;
					}
				}).catch(err => {

				});
		},
		// 点击添加按钮
		addApp(index, e) {
			const addItemDom = e.currentTarget.parentNode;
			const newItemDom = this.myAppList.length > 0 ? document.querySelectorAll('.myapp-item')[this.myAppList.length - 1] : undefined;
			const cloneItem = this.moreAppList[index];
			const addItemRect = e.currentTarget.parentNode.getBoundingClientRect(); // 元素宽高
			const isNextLine = this.myAppList.length % 4 === 0; // 新加元素是否在新的一行
			const newLeft = newItemDom ? isNextLine ? 0 : newItemDom.offsetLeft + addItemRect.width - 1 : 0; // 新加元素位置
			const newTop = newItemDom ? isNextLine ? (newItemDom.offsetTop + addItemRect.height - 1) : newItemDom.offsetTop : rootFontSize*0.9; // 新加元素位置
			this.addIndex = index;
			this.cloneData = {
				img: cloneItem.icon,
				title: cloneItem.title,
				left: addItemDom.offsetLeft,
				top: addItemDom.offsetTop,
				transX: newLeft - addItemDom.offsetLeft,
				transY: newTop - addItemDom.offsetTop
			}
			setTimeout(() => { this.isTreans = true; });
			setTimeout(() => {
				this.addIndex = -1;
				this.moreAppList.splice(index, 1);
				this.myAppList.push(cloneItem);
				setTimeout(() => {
					this.cloneData = {}
					this.isTreans = false;
				}, 400);
			}, 400);
		},
		delApp(index) {
			this.delIndex = index;
			const item = this.myAppList[index];
			setTimeout(() => {
				this.delIndex = -1;
				this.myAppList.splice(index, 1);
				this.moreAppList.push(item);
			}, 300);
    },
    toViewDetail(url) {
      if (url) {
        window.location.href = url;
      }
    },
    // 完成
    complete() {
      const idList = this.myAppList.map(item => item.moduleid);
      this.$axios.post('/hopeModule/editModuleSubV2', {
        // "aamid": this.$store.state.userInfoObj.aamid,
        "aamid": "010203",
        "addModules": idList
      }).then(res => {
        if (res.status === 200 && res.data.status === '000000') {
          Toast.success('编辑成功');
          this.isCanEdit = false;
        } else {
          Toast.fail('请重试');
        }
      }).catch(err => {
        Toast.fail('请重试');
      });
    }
	},
};
</script>
<style lang="less" scoped>
.my-concerns {
  min-height: 100%;
  background: #fff;
  position: relative;
}
@keyframes opacity0 {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.clone {
  width: 25%;
  height: 0.84rem;
  background: #ffffff;
  padding-top: 0.18rem;
  box-sizing: border-box;
  text-align: center;
  font-size: 0;
  border: 1px solid #eaeaea;
  position: absolute;
  z-index: 9;
  margin-bottom: 1px;
  transition: all 0.4s linear;
  img {
    display: inline-block;
    width: 0.32rem;
    height: 0.32rem;
  }
  h5 {
    font-size: 0.13rem;
    color: #232323;
    margin-top: 0.06rem;
    line-height: 0.18rem;
  }
}
.opacity0 {
  animation: opacity0 0.4s linear forwards;
}
.top-fixed {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  height: 0.5rem;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #EAEAEA;
  padding-right: 0.1rem;
  background: #fff;
  box-sizing: border-box;
  .back{
    width: 0.4rem;
    height: 100%;
    background: url('../../assets/images/icon_return@2x.png') no-repeat center center;
    background-size: 0.2rem 0.2rem;
  }
  .search{
    flex: 1;
    box-sizing: border-box;
    // padding: 0.05rem 0.1rem 0.05rem 0.4rem;
    background: #F1F1F1 url('../../assets/images/icon_search@2x.png') no-repeat 0.12rem center;
    background-size: 0.2rem 0.2rem;
    border-radius: 0.15rem;
    height: 0.3rem;
    line-height: 0.2rem;
    font-size: 0.14rem;
    color: #000;
  }
  .edit-btn {
    width: 0.5rem;
    height: 0.28rem;
    font-size: 0.14rem;
    color: #ffffff;
    background: #45a4b1;
    margin-left: 0.1rem;
    line-height: 0.28rem;
    text-align: center;
    &:active {
      background: rgb(45, 107, 116);
    }
  }
}

.content {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  position: relative;
  section {
    overflow: hidden;
    &:first-child {
      margin-bottom: 0.2rem;
    }
    h4 {
      height: 0.4rem;
      line-height: 0.4rem;
      text-align: center;
      font-size: 0.16rem;
      color: #3a3a3a;
      font-weight: 500;
    }
    ul {
      overflow: hidden;
      width: calc(100% + 5px);
      li {
        width: 25%;
        height: 0.94rem;
        background: #ffffff;
        padding: 0.18rem 0.04rem 0;
        box-sizing: border-box;
        float: left;
        text-align: center;
        font-size: 0;
        margin-left: -1px;
        margin-bottom: -1px;
        border: 1px solid #fff;
        position: relative;
        img {
          display: inline-block;
          width: 0.32rem;
          height: 0.32rem;
        }
        h5 {
          font-size: 0.13rem;
          color: #232323;
          margin-top: 0.06rem;
          line-height: 0.14rem;
          overflow : hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        span {
          width: 0.29rem;
          height: 0.29rem;
          position: absolute;
          right: 0;
          top: 0;
        }
        .add-btn {
          background: url("../../assets/images/button_delete@2x.png") no-repeat
            center center;
          background-size: 0.19rem 0.19rem;
        }
        .del-btn {
          background: url("../../assets/images/button_add_app@2x.png") no-repeat
            center center;
          background-size: 0.19rem 0.19rem;
        }
      }
      .ghost {
        opacity: 0;
      }
      @keyframes disappear {
        0% {
          width: 25%;
          opacity: 0;
        }
        20% {
          width: 0;
          opacity: 0;
        }
      }
      .trans {
        animation-name: disappear;
        animation-duration: 2s;
        animation-timing-function: linear;
      }
    }
    .myapp-list {
      .sortable-chosen {
        box-shadow: #666 0px 0px 10px;
        border: 1px solid #fff;
      }
    }
    .more-list {
      li {
        margin-bottom: 0;
      }
      .margin {
        margin-top: -1px;
      }
    }
    .showBorder {
      border: 1px solid #eaeaea;
    }
  }
}
.handle-bar {
  position: fixed;
  width: 100%;
  height: 0.5rem;
  background: #ffffff;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.11);
  bottom: 0;
  display: flex;
  padding: 0 0.1rem;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  .btn {
    font-size: 0.14rem;
    width: 0.58rem;
    height: 0.28rem;
    text-align: center;
    line-height: 0.28rem;
  }
  .cancel {
    color: #45a4b1;
  }
  .done {
    background: #45a4b1;
    color: #fff;
    :active {
      background: rgb(45, 107, 116);
    }
  }
}
</style>